.sendForm {
    max-width: 1280px;
    margin: 50px auto;
    /*background-color: antiquewhite;*/
    display: flex;
    justify-content: space-between;
    height: 40px;
}

.textarea {
    height: 40px;
    width: 1100px;
    border-radius: 15px;
    resize: none;
    overflow: hidden;
    padding: 10px 0 10px 10px;
}

.button {
    width: 120px;
    height: 40px;
    border-radius: 20px;
    background: #0066CC;
    font-family: Montserrat, sans-serif;
    font-weight: 600;
    font-size: 16px;
    color: #FFFFFF;
    border: none;


}

.button:hover {
   background: #3282cc;
}

