.message {
    max-width: 1280px;
    margin: 20px auto;
    display: flex;
    gap: 10px;
    justify-content: flex-end;
    flex-direction: column;
    /*background-color: antiquewhite;*/
    /*margin-right: 320px;*/
}

.imageAndText {
    display: flex;
    flex-direction: row-reverse;
}

.imageAndText img {
    width: 80px;
    height: 80px;

}

.text {
    display: flex;
    /*gap: 20px;*/
    flex-direction: column;
    width: 527px;
    background-color: #0066CC;
    border-radius: 10px;
    margin-right: 15px;

}

.text:after {

}

.name {
    text-align: right;
    font-family: Montserrat, sans-serif;
    font-weight: 600;
    font-size: 18px;
    color: #FFFFFF;
    padding: 10px 10px 0 10px;
    line-height: 22px;

}

.messageText {
    font-family: Montserrat, sans-serif;
    font-weight: 400;
    font-size: 16px;
    color: #FFFFFF;
    padding: 10px 10px 0 0;
    text-align: right;


}

.time {
    text-align: right;
    /*background-color: #a7d2a7;*/
    margin-right: 27px;
    font-family: Montserrat, sans-serif;
    font-weight: 600;
    font-size: 10px;
    line-height: 12px;
}