.friendMessage {
    max-width: 1280px;
    margin: 0 auto;
    display: flex;
    gap: 10px;
    justify-content: flex-end;
    flex-direction: column;
    /*background-color: rgba(74, 171, 79, 0.79);*/
}

.friendImageAndText {
    display: flex;

}

.friendImageAndText img {
    width: 80px;
    height: 80px;

}

.friendText {
    display: flex;
    /*gap: 20px;*/
    flex-direction: column;
    width: 527px;
    background-color: #F5F7FB;
    border-radius: 10px;
    margin-left: 15px;
}

.friendText:after {

}

.friendName {
    font-family: Montserrat, sans-serif;
    font-weight: 600;
    font-size: 16px;
    color: #000000;
    padding: 10px 10px 0 0;

}

.friendMessageText {
    font-family: Montserrat, sans-serif;
    font-weight: 400;
    font-size: 16px;
    color: #000000;
    padding: 10px 10px 0 0;
}

.friendTime {
    font-family: Montserrat, sans-serif;
    font-weight: 600;
    font-size: 10px;
    line-height: 12px;
    margin-left: 27px;
}
